.banner_{height:calc(100vh - 100px);position:relative}.banner_ *{height:100%}.banner_ .img_wrap{height:100%}.banner_ .img_wrap img{object-fit:cover}.banner_ .banner_txt{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);z-index:3;height:auto;text-align:center}.banner_ .banner_txt h2{color:#fff;font-size:3.8em;margin-bottom:20px;font-weight:700}.banner_ .banner_txt h2 span{color:rgba(0,0,0,0);-webkit-text-stroke:1px #fff}.banner_ .banner_btn{background:#f083b3;border:#f083b3;color:#fff}.banner_ .swiper .swiper-button-next,.banner_ .swiper .swiper-button-prev{display:none}.banner_ .swiper .swiper-wrapper .swiper-slide:after{content:"";position:absolute;left:0;top:0;width:100%;height:100%;background-color:rgba(0,0,0,.5);z-index:1}.banner_ .swiper .swiper-pagination{bottom:20px !important;top:auto !important;height:auto;display:flex;align-items:center;justify-content:center}.banner_ .swiper .swiper-pagination .swiper-pagination-bullet{background:#f083b3;opacity:.5}.banner_ .swiper .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active{opacity:1;width:12px;height:12px}